body {
  background-color: #f1f4f5;
  font-family: Roboto;
}
.btn-new {
  margin-bottom: 40px;
}
.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  height: 100%;
  padding: 20px;
  border-right: 1px solid #eee;
  background-color: #263238;
}
.sidebar h2 {
  color: white;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
}
.sidebar ul {
  margin-right: -21px;
  margin-left: -20px;
}
.sidebar ul a {
  color: rgba(163, 175, 183, 0.9);
}
.sidebar ul a:hover {
  background-color: #242F35;
}
.content {
  padding-left: 60px;
  padding-right: 60px;
}
.content h2 {
  font-weight: 100;
  margin-bottom: 40px;
}
.content .clear {
  clear: both;
}
.info-box {
  width: 100%;
  height: auto;
  margin-top: 20px;
  padding: 20px;
  background-color: white;
  margin-bottom: 20px;
}
.info-box h1 {
  font-weight: 100;
  padding-top: 0;
}
.info-box h2 {
  font-weight: 100;
  margin-top: 0px;
  padding: 0;
}
.featured-banner {
  width: 100%;
  height: 300px;
  background: url("http://tutorialedge.net/public/images/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
